<template>
  <div class="homologacoes-ccl">
    <!-- Homologaes Recentes -->
    <div class="section-card">
      <div class="section-header">
        <h3> Homologaes & Decises da Autoridade</h3>
        <div class="section-actions">
          <button @click="carregarHomologacoes" class="btn-primary">
             Atualizar
          </button>
        </div>
      </div>

      <div class="homologacoes-container" v-if="homologacoes.length > 0">
        <div 
          v-for="homologacao in homologacoesPaginadas" 
          :key="homologacao.id" 
          class="homologacao-card"
        >
          <div class="homologacao-header">
            <div class="homologacao-info">
              <h4>{{ homologacao.numeroAta }}</h4>
              <p class="data">{{ formatDate(homologacao.dataHomologacao) }}</p>
            </div>
            <div class="homologacao-status">
              <span class="status-badge" :class="getStatusClass(homologacao.tipo)">
                {{ formatStatus(homologacao.tipo) }}
              </span>
            </div>
          </div>
          
          <div class="homologacao-actions">
            <button @click="visualizarAta(homologacao)" class="btn-small btn-primary">
               Ver Ata
            </button>
            <button @click="baixarPDF(homologacao)" class="btn-small btn-secondary">
               PDF
            </button>
            <button @click="verDCBs(homologacao)" class="btn-small btn-success">
               DCBs
            </button>
            <!-- Boto de Deciso da Autoridade (apenas para processos pendentes) -->
            <button 
              v-if="homologacao.tipo === 'diligencia'" 
              @click="decidirHomologacao(homologacao)" 
              class="btn-small btn-warning"
              title="Tomar deciso final como Autoridade Competente"
            >
               Decidir
            </button>
            <!-- Boto de Confirmao CCL (para homologao direta) -->
            <button 
              v-if="homologacao.tipo === 'diligencia_direta'" 
              @click="confirmarHomologacaoCCL(homologacao)" 
              class="btn-small btn-success"
              title="Confirmar homologao direta da CCL"
            >
               Confirmar
            </button>
            <!-- Boto para enviar de volta para CPM -->
            <button 
              v-else-if="homologacao.tipo === 'homologada'" 
              @click="enviarParaCPM(homologacao)"
              class="btn-small btn-success"
              title="Enviar processo homologado de volta para CPM para expedio de DCBs"
            >
               Enviar para CPM
            </button>
            <!-- Status final para processos j concludos -->
            <span 
              v-else-if="homologacao.tipo === 'incluindo_marcas'" 
              class="btn-small btn-success"
              title="Processo concludo - produtos no catlogo"
            >
               Concludo
            </span>
            <span 
              v-else-if="homologacao.tipo === 'indeferida'" 
              class="btn-small btn-danger"
              title="Processo foi indeferido"
            >
               Indeferido
            </span>
          </div>
        </div>
      </div>

      <div v-else class="empty-state">
        <p>Nenhuma homologao encontrada</p>
      </div>

      <!-- Paginao -->
      <div v-if="homologacoes.length > 0" class="pagination-controls">
        <button 
          @click="paginaAnterior" 
          :disabled="paginaAtual === 1"
          class="btn-pagination"
        >
           Anterior
        </button>
        <span class="pagination-info">
          Pgina {{ paginaAtual }} de {{ totalPaginas }}
        </span>
        <button 
          @click="proximaPagina" 
          :disabled="paginaAtual === totalPaginas"
          class="btn-pagination"
        >
          Prxima 
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { supabase } from '../../services/supabase'
import TramitacaoProcessosService from '../../services/tramitacaoProcessosService'

export default {
  name: 'HomologacoesCCL',
  data() {
    return {
      homologacoes: [],
      loading: false,
      paginaAtual: 1,
      itensPorPagina: 5
    }
  },
  computed: {
    totalPaginas() {
      return Math.ceil(this.homologacoes.length / this.itensPorPagina)
    },
    homologacoesPaginadas() {
      const inicio = (this.paginaAtual - 1) * this.itensPorPagina
      const fim = inicio + this.itensPorPagina
      return this.homologacoes.slice(inicio, fim)
    },
    currentTenantId() {
      // Tentar mltiplas fontes para garantir que temos o tenant_id
      return this.$store.getters.getTenantId || 
             this.$store.state.user?.user?.user_metadata?.tenant_id ||
             this.$store.state.user?.tenant_id ||
             '8329e25e-8a01-4fc6-ab15-7a83dd874bde' // Fallback para o tenant atual
    }
  },
  mounted() {
    this.carregarHomologacoes()
  },
  methods: {
    async carregarHomologacoes() {
      if (!this.currentTenantId) {
        console.log(' HomologacoesCCL: Sem tenant_id')
        return
      }
      
      this.loading = true
      try {
        // Buscar processos que podem ser homologados
        const { data, error } = await supabase
          .from('processos_administrativos')
          .select(`
            id,
            numero_processo,
            status,
            ata_emitida_ccl_em,
            created_at,
            updated_at,
            tenant_id,
            ata_julgamento_ccl
          `)
          .eq('tenant_id', this.currentTenantId)
          .in('status', ['ata_julgamento_ccl_homologacao', 'ata_julgamento_ccl_indeferimento', 'diligencia_ccl', 'homologado', 'indeferido'])
          .order('updated_at', { ascending: false })
          .limit(20)

        if (error) throw error

        // Transformar dados para o formato esperado
        this.homologacoes = data?.map(processo => ({
          id: processo.id,
          numeroAta: `${processo.numero_processo}`,
          dataHomologacao: processo.ata_emitida_ccl_em || processo.updated_at || processo.created_at,
          tipo: this.determinarTipo(processo.status, processo.ata_julgamento_ccl),
          processo: processo
        })) || []

      } catch (error) {
        console.error('Erro ao carregar homologaes:', error)
        this.$swal({
          title: ' Erro',
          text: 'Erro ao carregar homologaes',
          icon: 'error'
        })
      } finally {
        this.loading = false
      }
    },

    // Deciso da Autoridade - Funo Principal
    decidirHomologacao(homologacao) {
      this.$swal({
        title: ' Deciso da Autoridade',
        text: `Processo: ${homologacao.numeroAta}`,
        icon: 'question',
        showCancelButton: true,
        showDenyButton: true,
        confirmButtonText: ' Homologar',
        denyButtonText: ' Indeferir',
        cancelButtonText: ' Diligncia'
      }).then((result) => {
        if (result.isConfirmed) {
          this.processarDecisao(homologacao, 'HOMOLOGADO')
        } else if (result.isDenied) {
          this.processarDecisao(homologacao, 'INDEFERIDO')
        } else if (result.isDismissed) {
          this.processarDecisao(homologacao, 'EM DILIGNCIA')
        }
      })
    },

    async processarDecisao(homologacao, decisao) {
      try {
        // Registrar deciso no banco (adapte conforme necessrio)
        console.log(' Registrando deciso:', {
          processoId: homologacao.id,
          numeroAta: homologacao.numeroAta,
          decisao: decisao,
          dataDecisao: new Date().toISOString()
        })

        // Atualizar processo no banco
        if (homologacao.processo) {
          const novoStatus = decisao === 'HOMOLOGADO' ? 'homologado' : 
                           decisao === 'INDEFERIDO' ? 'indeferido' : 'diligencia_ccl'
          
          const { error } = await supabase
            .from('processos_administrativos')
            .update({ 
              status: novoStatus,
              updated_at: new Date().toISOString()
            })
            .eq('id', homologacao.processo.id)
            .eq('tenant_id', this.currentTenantId)

          if (error) throw error
        }

        // Mostrar resultado
        const cores = {
          'HOMOLOGADO': '#28a745',
          'INDEFERIDO': '#dc3545', 
          'EM DILIGNCIA': '#ffc107'
        }

        this.$swal({
          title: ' Deciso Registrada',
          html: `
            <div style="text-align: center; padding: 20px;">
              <h4 style="color: ${cores[decisao]};">${decisao}</h4>
              <p><strong>Processo:</strong> ${homologacao.numeroAta}</p>
              <p><strong>Deciso registrada em:</strong> ${this.formatDate(new Date())}</p>
            </div>
          `,
          icon: 'success',
          timer: 3000
        })
        
        // Recarregar dados
        await this.carregarHomologacoes()

      } catch (error) {
        console.error('Erro ao processar deciso:', error)
        this.$swal({
          title: ' Erro',
          text: `Erro ao registrar deciso: ${error.message}`,
          icon: 'error'
        })
      }
    },

    determinarTipo(status, ataJulgamento = '') {
      switch (status) {
        case 'ata_julgamento_ccl_homologacao':
          // Se tem marcao de homologao direta no texto,  para confirmao CCL
          const isHomologacaoDireta = ataJulgamento && ataJulgamento.includes('homologao direta')
          return isHomologacaoDireta ? 'diligencia_direta' : 'diligencia'
        case 'ata_julgamento_ccl_indeferimento':
          return 'diligencia' // Recomendado para indeferimento - AGUARDA DECISO DA AUTORIDADE
        case 'diligencia_ccl':
          return 'diligencia' // Em diligncia - AGUARDA DECISO DA AUTORIDADE
        case 'homologado':
          return 'homologada' // J FOI DECIDIDO - HOMOLOGADO PELA AUTORIDADE - PRXIMO: EXPEDIR DCBS
        case 'expedindo_dcbs':
          return 'expedindo_dcbs' // EXPEDINDO DCBS - PRXIMO: INCLUIR NO CATLOGO
        case 'incluindo_marcas':
          return 'incluindo_marcas' // PROCESSO CONCLUDO - PRODUTOS NO CATLOGO
        case 'indeferido':
          return 'indeferida' // J FOI DECIDIDO - INDEFERIDO PELA AUTORIDADE
        default:
          return 'diligencia'
      }
    },

    getStatusClass(tipo) {
      switch (tipo) {
        case 'homologada': return 'status-aprovado'
        case 'indeferida': return 'status-reprovado'
        case 'diligencia': return 'status-pendente'
        default: return 'status-padrao'
      }
    },

    formatStatus(tipo) {
      switch (tipo) {
        case 'homologada': return ' HOMOLOGADA - AGUARDA ENVIO PARA CPM'
        case 'expedindo_dcbs': return ' EXPEDINDO DCBs - AGUARDA INCLUSO NO CATLOGO'
        case 'incluindo_marcas': return ' CONCLUDO - PRODUTOS NO CATLOGO'
        case 'indeferida': return ' INDEFERIDA (FINAL)'
        case 'diligencia': return ' AGUARDANDO DECISO DA AUTORIDADE'
        case 'diligencia_direta': return ' AGUARDANDO CONFIRMAO CCL'
        default: return tipo
      }
    },

    // Confirmao da Homologao Direta da CCL
    confirmarHomologacaoCCL(homologacao) {
      this.$swal({
        title: ' Confirmar Homologao Direta',
        html: `
          <div style="text-align: left; padding: 15px;">
            <div style="background: #e8f5e8; padding: 15px; border-radius: 8px; margin-bottom: 20px;">
              <h4 style="margin: 0 0 10px 0; color: #2e7d32;"> Processo: ${homologacao.numeroAta}</h4>
              <p style="margin: 5px 0;"><strong>Status:</strong> Aprovado pela CCL para homologao direta</p>
              <p style="margin: 5px 0;"><strong>Data:</strong> ${this.formatDate(homologacao.dataHomologacao)}</p>
            </div>
            
            <div style="background: #fff3cd; padding: 12px; border-radius: 8px; margin-bottom: 15px;">
              <h5 style="margin: 0 0 8px 0; color: #856404; font-size: 14px;"> Homologao Direta:</h5>
              <p style="margin: 0; font-size: 13px;">
                A CCL j analisou e aprovou este processo. A confirmao ir homologar definitivamente 
                e emitir as DCBs automaticamente.
              </p>
            </div>
            
            <div style="margin-bottom: 15px;">
              <label style="display: block; font-weight: bold; margin-bottom: 8px;"> Observaes da Homologao:</label>
              <textarea id="observacoesHomologacao" class="swal2-textarea" rows="3" 
                placeholder="Observaes adicionais sobre a homologao (opcional)..." 
                style="width: 100%; box-sizing: border-box;"></textarea>
            </div>
          </div>
        `,
        showCancelButton: true,
        confirmButtonText: ' Confirmar Homologao',
        cancelButtonText: ' Cancelar',
        confirmButtonColor: '#28a745',
        width: '500px',
        preConfirm: () => {
          const observacoes = document.getElementById('observacoesHomologacao').value
          return { observacoes: observacoes.trim() }
        }
      }).then((result) => {
        if (result.isConfirmed) {
          this.processarConfirmacaoHomologacao(homologacao, result.value.observacoes)
        }
      })
    },

    async processarConfirmacaoHomologacao(homologacao, observacoes) {
      try {
        // Atualizar processo para homologado
        const { error } = await supabase
          .from('processos_administrativos')
          .update({ 
            status: 'homologado',
            updated_at: new Date().toISOString()
          })
          .eq('id', homologacao.processo.id)
          .eq('tenant_id', this.currentTenantId)

        if (error) throw error

        // Mostrar resultado
        this.$swal({
          title: ' Processo Homologado!',
          html: `
            <div style="text-align: center; padding: 20px;">
              <h4 style="color: #28a745;">HOMOLOGAO CONFIRMADA</h4>
              <p><strong>Processo:</strong> ${homologacao.numeroAta}</p>
              <p><strong>Homologado em:</strong> ${this.formatDate(new Date())}</p>
              <div style="background: #e8f5e8; padding: 12px; border-radius: 8px; margin-top: 15px;">
                <p style="margin: 0; font-size: 14px;">
                   <strong>DCBs sero emitidas automaticamente</strong>
                </p>
              </div>
            </div>
          `,
          icon: 'success',
          timer: 4000
        })
        
        // Recarregar dados
        await this.carregarHomologacoes()

      } catch (error) {
        console.error('Erro ao confirmar homologao:', error)
        this.$swal({
          title: ' Erro',
          text: `Erro ao confirmar homologao: ${error.message}`,
          icon: 'error'
        })
      }
    },

    formatDate(dateInput) {
      if (!dateInput) return ''
      const date = new Date(dateInput)
      return date.toLocaleDateString('pt-BR')
    },

    async visualizarAta(homologacao) {
      try {
        // Buscar o contedo da ata no processo
        const { data: processo, error } = await supabase
          .from('processos_administrativos')
          .select('ata_julgamento_ccl, numero_processo, status')
          .eq('id', homologacao.processo.id)
          .eq('tenant_id', this.currentTenantId)
          .single()

        if (error) throw error

        const conteudoAta = processo.ata_julgamento_ccl || 'Contedo da ata no encontrado.'

        this.$swal({
          title: ` Ata de Julgamento - ${processo.numero_processo}`,
          html: `
            <div style="text-align: left; padding: 20px; max-height: 400px; overflow-y: auto;">
              <div style="background: #f8f9fa; padding: 15px; border-radius: 8px; margin-bottom: 15px; border-left: 4px solid #007bff;">
                <h4 style="margin: 0 0 10px 0; color: #007bff;"> Informaes do Processo</h4>
                <p style="margin: 5px 0;"><strong>Processo:</strong> ${processo.numero_processo}</p>
                <p style="margin: 5px 0;"><strong>Status:</strong> ${processo.status}</p>
                <p style="margin: 5px 0;"><strong>Data:</strong> ${this.formatDate(homologacao.dataHomologacao)}</p>
              </div>
              
              <div style="background: white; padding: 15px; border-radius: 8px; border: 1px solid #dee2e6;">
                <h5 style="margin: 0 0 15px 0; color: #333; border-bottom: 1px solid #eee; padding-bottom: 10px;"> Contedo da Ata:</h5>
                <div style="white-space: pre-wrap; font-family: 'Courier New', monospace; font-size: 13px; line-height: 1.5; color: #333;">
                  ${conteudoAta}
                </div>
              </div>
            </div>
          `,
          showCancelButton: true,
          cancelButtonText: ' Fechar',
          showConfirmButton: true,
          confirmButtonText: ' Baixar PDF',
          confirmButtonColor: '#007bff',
          width: '700px',
          customClass: {
            htmlContainer: 'custom-swal-container'
          }
        }).then((result) => {
          if (result.isConfirmed) {
            this.baixarPDF(homologacao)
          }
        })

      } catch (error) {
        console.error('Erro ao visualizar ata:', error)
        this.$swal({
          title: ' Erro',
          text: `Erro ao carregar ata: ${error.message}`,
          icon: 'error'
        })
      }
    },

    async baixarPDF(homologacao) {
      try {
        // Buscar dados completos do processo
        const { data: processo, error } = await supabase
          .from('processos_administrativos')
          .select('*')
          .eq('id', homologacao.processo.id)
          .eq('tenant_id', this.currentTenantId)
          .single()

        if (error) throw error

        // Gerar PDF usando jsPDF
        const { jsPDF } = window.jspdf || require('jspdf')
        
        if (!jsPDF) {
          throw new Error('Biblioteca jsPDF no encontrada')
        }

        const doc = new jsPDF()
        const pageWidth = doc.internal.pageSize.getWidth()
        const margin = 20

        // Cabealho
        doc.setFontSize(16)
        doc.setFont('helvetica', 'bold')
        doc.text('ATA DE JULGAMENTO DA CCL', pageWidth / 2, 30, { align: 'center' })
        
        doc.setFontSize(12)
        doc.setFont('helvetica', 'normal')
        doc.text(`Processo: ${processo.numero_processo}`, margin, 50)
        doc.text(`Data: ${this.formatDate(homologacao.dataHomologacao)}`, margin, 60)
        doc.text(`Status: ${processo.status}`, margin, 70)
        
        // Linha separadora
        doc.setLineWidth(0.5)
        doc.line(margin, 80, pageWidth - margin, 80)
        
        // Contedo da ata
        let yPosition = 95
        doc.setFontSize(11)
        doc.setFont('helvetica', 'normal')
        
        const conteudoAta = processo.ata_julgamento_ccl || 'Contedo da ata no disponvel.'
        const lines = doc.splitTextToSize(conteudoAta, pageWidth - 2 * margin)
        
        lines.forEach(line => {
          if (yPosition > 270) { // Nova pgina se necessrio
            doc.addPage()
            yPosition = 20
          }
          doc.text(line, margin, yPosition)
          yPosition += 6
        })
        
        // Rodap
        const totalPages = doc.internal.getNumberOfPages()
        for (let i = 1; i <= totalPages; i++) {
          doc.setPage(i)
          doc.setFontSize(8)
          doc.setFont('helvetica', 'italic')
          doc.text(`Pgina ${i} de ${totalPages}`, pageWidth / 2, 285, { align: 'center' })
          doc.text(`Gerado em: ${new Date().toLocaleString('pt-BR')}`, pageWidth - margin, 285, { align: 'right' })
        }
        
        // Download do arquivo
        const fileName = `Ata_${processo.numero_processo.replace('/', '_')}_${new Date().toISOString().split('T')[0]}.pdf`
        doc.save(fileName)
        
        this.$swal({
          title: ' PDF Gerado!',
          text: `Arquivo "${fileName}" baixado com sucesso`,
          icon: 'success',
          timer: 3000
        })

      } catch (error) {
        console.error('Erro ao gerar PDF:', error)
        this.$swal({
          title: ' Erro ao gerar PDF',
          text: 'No foi possvel gerar o PDF. Verifique se a biblioteca jsPDF est carregada.',
          icon: 'error'
        })
      }
    },

    async verDCBs(homologacao) {
      try {
        // Buscar produtos associados ao processo
        const { data: produtos, error } = await supabase
          .from('produtos')
          .select(`
            id,
            nome,
            codigo,
            status,
            dcb_numero,
            dcb_data_emissao,
            dcb_status,
            created_at
          `)
          .eq('processo_id', homologacao.processo.id)
          .eq('tenant_id', this.currentTenantId)
          .order('created_at', { ascending: false })

        if (error) throw error

        const produtosComDCB = produtos?.filter(p => p.dcb_numero) || []
        const produtosSemDCB = produtos?.filter(p => !p.dcb_numero) || []

        this.$swal({
          title: ` DCBs do Processo ${homologacao.numeroAta}`,
          html: `
            <div style="text-align: left; padding: 20px; max-height: 500px; overflow-y: auto;">
              <div style="background: #f8f9fa; padding: 15px; border-radius: 8px; margin-bottom: 20px; border-left: 4px solid #007bff;">
                <h4 style="margin: 0 0 10px 0; color: #007bff;"> Resumo do Processo</h4>
                <p style="margin: 5px 0;"><strong>Processo:</strong> ${homologacao.numeroAta}</p>
                <p style="margin: 5px 0;"><strong>Status:</strong> ${this.formatStatus(homologacao.tipo)}</p>
                <p style="margin: 5px 0;"><strong>Data:</strong> ${this.formatDate(homologacao.dataHomologacao)}</p>
                <p style="margin: 5px 0;"><strong>Total de Produtos:</strong> ${produtos?.length || 0}</p>
              </div>

              ${produtosComDCB.length > 0 ? `
                <div style="background: #d4edda; padding: 15px; border-radius: 8px; margin-bottom: 15px; border-left: 4px solid #28a745;">
                  <h5 style="margin: 0 0 15px 0; color: #155724;"> DCBs Emitidas (${produtosComDCB.length})</h5>
                  ${produtosComDCB.map(produto => `
                    <div style="background: white; padding: 12px; border-radius: 6px; margin-bottom: 10px; border: 1px solid #c3e6cb;">
                      <div style="display: flex; justify-content: space-between; align-items: center; margin-bottom: 8px;">
                        <strong style="color: #155724;">${produto.nome}</strong>
                        <span style="background: #28a745; color: white; padding: 2px 8px; border-radius: 12px; font-size: 11px;">
                          ${produto.dcb_status || 'ATIVA'}
                        </span>
                      </div>
                      <p style="margin: 4px 0; font-size: 13px;"><strong>DCB:</strong> ${produto.dcb_numero}</p>
                      <p style="margin: 4px 0; font-size: 13px;"><strong>Cdigo:</strong> ${produto.codigo}</p>
                      <p style="margin: 4px 0; font-size: 13px;"><strong>Emisso:</strong> ${produto.dcb_data_emissao ? this.formatDate(produto.dcb_data_emissao) : 'No informada'}</p>
                    </div>
                  `).join('')}
                </div>
              ` : ''}

              ${produtosSemDCB.length > 0 ? `
                <div style="background: #fff3cd; padding: 15px; border-radius: 8px; border-left: 4px solid #ffc107;">
                  <h5 style="margin: 0 0 15px 0; color: #856404;"> Aguardando DCB (${produtosSemDCB.length})</h5>
                  ${produtosSemDCB.map(produto => `
                    <div style="background: white; padding: 12px; border-radius: 6px; margin-bottom: 10px; border: 1px solid #ffeaa7;">
                      <div style="display: flex; justify-content: space-between; align-items: center; margin-bottom: 8px;">
                        <strong style="color: #856404;">${produto.nome}</strong>
                        <span style="background: #ffc107; color: #212529; padding: 2px 8px; border-radius: 12px; font-size: 11px;">
                          ${produto.status || 'PENDENTE'}
                        </span>
                      </div>
                      <p style="margin: 4px 0; font-size: 13px;"><strong>Cdigo:</strong> ${produto.codigo}</p>
                      <p style="margin: 4px 0; font-size: 13px;"><strong>Status:</strong> Aguardando expedio de DCB</p>
                    </div>
                  `).join('')}
                </div>
              ` : ''}

              ${produtos?.length === 0 ? `
                <div style="background: #f8d7da; padding: 15px; border-radius: 8px; border-left: 4px solid #dc3545; text-align: center;">
                  <h5 style="margin: 0 0 10px 0; color: #721c24;"> Nenhum produto encontrado</h5>
                  <p style="margin: 0; color: #721c24;">
                    No h produtos associados a este processo ou ainda no foram cadastrados no sistema.
                  </p>
                </div>
              ` : ''}

              <div style="background: #e3f2fd; padding: 12px; border-radius: 8px; margin-top: 15px;">
                <p style="margin: 0; font-size: 12px; color: #1976d2;">
                   <strong>Dica:</strong> As DCBs so emitidas aps a homologao e tramitao para a CPM.
                </p>
              </div>
            </div>
          `,
          showCancelButton: true,
          cancelButtonText: ' Fechar',
          showConfirmButton: produtosComDCB.length > 0,
          confirmButtonText: ' Baixar Relatrio DCBs',
          confirmButtonColor: '#007bff',
          width: '600px'
        }).then((result) => {
          if (result.isConfirmed && produtosComDCB.length > 0) {
            this.gerarRelatorioDCBs(homologacao, produtosComDCB)
          }
        })

      } catch (error) {
        console.error('Erro ao carregar DCBs:', error)
        this.$swal({
          title: ' Erro',
          text: `Erro ao carregar DCBs: ${error.message}`,
          icon: 'error'
        })
      }
    },

    async gerarRelatorioDCBs(homologacao, produtos) {
      try {
        const { jsPDF } = window.jspdf || require('jspdf')
        
        if (!jsPDF) {
          throw new Error('Biblioteca jsPDF no encontrada')
        }

        const doc = new jsPDF()
        const pageWidth = doc.internal.pageSize.getWidth()
        const margin = 20

        // Cabealho
        doc.setFontSize(14)
        doc.setFont('helvetica', 'bold')
        doc.text('RELATRIO DE DCBs', pageWidth / 2, 30, { align: 'center' })
        
        doc.setFontSize(12)
        doc.setFont('helvetica', 'normal')
        doc.text(`Processo: ${homologacao.numeroAta}`, margin, 50)
        doc.text(`Data: ${this.formatDate(homologacao.dataHomologacao)}`, margin, 60)
        doc.text(`Total de DCBs: ${produtos.length}`, margin, 70)
        
        // Linha separadora
        doc.setLineWidth(0.5)
        doc.line(margin, 80, pageWidth - margin, 80)
        
        // Lista de DCBs
        let yPosition = 95
        doc.setFontSize(10)
        
        produtos.forEach((produto, index) => {
          if (yPosition > 250) {
            doc.addPage()
            yPosition = 20
          }
          
          doc.setFont('helvetica', 'bold')
          doc.text(`${index + 1}. ${produto.nome}`, margin, yPosition)
          yPosition += 8
          
          doc.setFont('helvetica', 'normal')
          doc.text(`DCB: ${produto.dcb_numero}`, margin + 5, yPosition)
          yPosition += 6
          doc.text(`Cdigo: ${produto.codigo}`, margin + 5, yPosition)
          yPosition += 6
          doc.text(`Status: ${produto.dcb_status || 'ATIVA'}`, margin + 5, yPosition)
          yPosition += 6
          doc.text(`Emisso: ${produto.dcb_data_emissao ? this.formatDate(produto.dcb_data_emissao) : 'No informada'}`, margin + 5, yPosition)
          yPosition += 15
        })
        
        // Rodap
        const totalPages = doc.internal.getNumberOfPages()
        for (let i = 1; i <= totalPages; i++) {
          doc.setPage(i)
          doc.setFontSize(8)
          doc.setFont('helvetica', 'italic')
          doc.text(`Pgina ${i} de ${totalPages}`, pageWidth / 2, 285, { align: 'center' })
          doc.text(`Gerado em: ${new Date().toLocaleString('pt-BR')}`, pageWidth - margin, 285, { align: 'right' })
        }
        
        const fileName = `Relatorio_DCBs_${homologacao.numeroAta.replace('/', '_')}_${new Date().toISOString().split('T')[0]}.pdf`
        doc.save(fileName)
        
        this.$swal({
          title: ' Relatrio Gerado!',
          text: `Arquivo "${fileName}" baixado com sucesso`,
          icon: 'success',
          timer: 3000
        })

      } catch (error) {
        console.error('Erro ao gerar relatrio:', error)
        this.$swal({
          title: ' Erro ao gerar relatrio',
          text: 'No foi possvel gerar o relatrio de DCBs.',
          icon: 'error'
        })
      }
    },

    proximaPagina() {
      if (this.paginaAtual < this.totalPaginas) {
        this.paginaAtual++
      }
    },

    paginaAnterior() {
      if (this.paginaAtual > 1) {
        this.paginaAtual--
      }
    },

    // Enviar processo homologado de volta para CPM
    async enviarParaCPM(homologacao) {
      this.$swal({
        title: ' Enviar para CPM',
        html: `
          <div style="text-align: left; padding: 15px;">
            <div style="background: #e8f5e8; padding: 15px; border-radius: 8px; margin-bottom: 20px;">
              <h4 style="margin: 0 0 10px 0; color: #2e7d32;"> Processo: ${homologacao.numeroAta}</h4>
              <p style="margin: 5px 0;"><strong>Status:</strong> Homologado pela Autoridade</p>
              <p style="margin: 5px 0;"><strong>Data:</strong> ${this.formatDate(homologacao.dataHomologacao)}</p>
            </div>
            
            <div style="background: #fff3cd; padding: 12px; border-radius: 8px; margin-bottom: 15px;">
              <h5 style="margin: 0 0 8px 0; color: #856404; font-size: 14px;"> Tramitao para CPM:</h5>
              <p style="margin: 0; font-size: 13px;">
                O processo ser enviado de volta para a CPM para:<br>
                 Expedio das DCBs<br>
                 Incluso dos produtos no catlogo eletrnico
              </p>
            </div>
            
            <div style="margin-bottom: 15px;">
              <label style="display: block; font-weight: bold; margin-bottom: 8px;"> Observaes da Tramitao:</label>
              <textarea id="observacoesTramitacao" class="swal2-textarea" rows="3" 
                placeholder="Observaes sobre o envio para CPM (opcional)..." 
                style="width: 100%; box-sizing: border-box;"></textarea>
            </div>
          </div>
        `,
        showCancelButton: true,
        confirmButtonText: ' Enviar para CPM',
        cancelButtonText: ' Cancelar',
        confirmButtonColor: '#28a745',
        width: '500px',
        preConfirm: () => {
          const observacoes = document.getElementById('observacoesTramitacao').value
          return { observacoes: observacoes.trim() }
        }
      }).then((result) => {
        if (result.isConfirmed) {
          this.processarEnvioParaCPM(homologacao, result.value.observacoes)
        }
      })
    },

    async processarEnvioParaCPM(homologacao, observacoes) {
      try {
        // Tramitar processo usando o servio (envia email automaticamente)
        const resultado = await TramitacaoProcessosService.enviarProcessoFlexivel(
          homologacao.processo.id,
          'expedindo_dcbs',
          'ENVIO_PARA_CPM',
          `Processo homologado enviado para CPM para expedio de DCBs e incluso no catlogo. ${observacoes ? 'Observaes: ' + observacoes : ''}`
        )

        if (!resultado.sucesso) throw new Error('Erro na tramitao')

        // Mostrar resultado
        this.$swal({
          title: ' Enviado para CPM!',
          html: `
            <div style="text-align: center; padding: 20px;">
              <h4 style="color: #28a745;">PROCESSO TRAMITADO COM SUCESSO</h4>
              <p><strong>Processo:</strong> ${homologacao.numeroAta}</p>
              <p><strong>Enviado em:</strong> ${this.formatDate(new Date())}</p>
              <div style="background: #e3f2fd; padding: 12px; border-radius: 8px; margin-top: 15px;">
                <p style="margin: 0; font-size: 14px;">
                   <strong>Processo agora est com a CPM</strong>
                </p>
                <p style="margin: 5px 0 0 0; font-size: 12px;">
                  A CPM ir expedir as DCBs e incluir no catlogo
                </p>
              </div>
            </div>
          `,
          icon: 'success',
          timer: 4000
        })
        
        // Recarregar dados (processo sair da lista da CCL)
        await this.carregarHomologacoes()

      } catch (error) {
        console.error('Erro ao enviar para CPM:', error)
        this.$swal({
          title: ' Erro',
          text: `Erro ao enviar para CPM: ${error.message}`,
          icon: 'error'
        })
      }
    }
  }
}
</script>

